import React, { FC } from 'react';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, Especie } from '../../../../models/dtos/ger/especie';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'abreviacao',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const EspecieSearchView: FC<SearchViewProps<Especie>> = props => {
  const columns: ColumnsProps[] = [
    {
      dataField: 'abreviacao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Abreviação',
    },
    {
      dataField: 'descricao',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
  ];
  return (
    <WindowComponent title="Manutenção de Espécie">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'EspecieEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default EspecieSearchView;
